<template>
  <div class="main">
    <div class="item-name">
      <span class="title-text text">{{ Items.Title }}:</span>
    </div>
    <div class="item-detail">
      <span v-if="Items.AllSelected" class="text detail"
            :class="[Default.includes('全部') ? 'selected' : 'not-selectde']"
            v-on:click="SelectUse({item: '全部',index: 9999})"
      >全部</span>
      <span v-for="(item, index) in Items.SelectItems"
            :class="[Default.includes(item) ? 'selected' : 'not-selectde']"
            class="text detail"
            v-on:click="SelectUse({item,index})"
      >{{ item }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "SizerItem",
  data(){
    return {
      Default: this.Items.Default,
    }
  },
  props: {
    Items: {type: Object, required: true},//筛选对象
    ItemsIndex: {type: Number, required: true},//对象索引
    StoreName: {type:String, required: true} //Store名称
  },
  methods:{
    Duoxuan(item){
      if (item.item === '全部') {
        this.Default = []
        this.Default.push(item.item)
      } else {
        let i
        i = this.Default.indexOf('全部')
        if (i > -1) { this.Default = [] }
        i = this.Default.indexOf(item.item)
        i>-1 ? this.Default.splice(i,1) : this.Default.push(item.item)}
    },
    Danxuan(item){
      this.Default = []
      this.Default.push(item.item)
    },
    SelectUse(item){
      const i = this.Default.indexOf(item.item)
      this.Items.MultipleChoice ? this.Duoxuan(item) : this.Danxuan(item)
      this.$store.commit(this.StoreName + '/SetSelected', {class:this.Items.Class,data:this.Default})
    }
  },
}
</script>

<style scoped>
.main {
  display: flex;
  line-height: normal;
}
.text {
  font-size: 6px;
}
.title-text {
  color: cornflowerblue;
  margin-left: 5px;
}
.detail {
  margin-left: 15px;
  cursor:pointer
}
.not-selectde {
  color: darkolivegreen;
}
.selected {
  font-size: 8px !important;
  color: crimson;
}
</style>