<template>
  <el-button type="primary" round @click="open">新增设计图</el-button>
  <div class="main-windows">
    <el-drawer
        v-model="dialog"
        title="设计图上传器"
        :before-close="handleClose"
        direction="rtl"
        :size="800"
        custom-class="demo-drawer">
      <!--        :append-to-body="true"-->
      <el-container height="100%">
        <el-main>
          <el-scrollbar height="100%">
            <main-box />
          </el-scrollbar>
        </el-main>
        <el-footer>
          <el-space :size="size" :spacer="spacer">
            <el-button type="primary">保存</el-button>
            <el-button @click="cancelForm">取消</el-button>
          </el-space>
        </el-footer>
      </el-container>
    </el-drawer>
  </div>
</template>

<script>
import { h } from 'vue'
import {ElDivider, ElMessageBox } from 'element-plus'
import MainBox from "@/views/banxingguanli/banxingliebiao/modules/funmodules/BanxingGuanli/MainBox";

export default {
  name:'BanxingGuanli',
  data(){
    return {
      dialog: false,
      size: 10,
      spacer: h(ElDivider, { direction: 'vertical' }),
    }
  },
  methods:{
    handleClose(done){
      if (this.loading) {
        return
      }
      const boxIofo = {
        type:'warning',
        cancelButtonText:'取消',
        confirmButtonText:'确定',
        showClose:false
      }
      this.$confirm('有更改为保存，是否关闭？',boxIofo)
          .then(() => {
            this.loading = true
            this.timer = setTimeout(() => {
              done()
              // 动画关闭需要一定的时间
              setTimeout(() => {
                this.loading = false
              }, 400)
            }, 2000)
          })
          .catch(() => {
            this.$emit('dialog',false)
          })
    },
    cancelForm(){
      this.close()
    },
    close(){
      this.dialog = false
    },
    open(){
      this.dialog = true
    }
  },
  components:{
    MainBox
  }
}
</script>

<style lang="scss" scoped>
.main-windows {
  line-height: normal;
}

/deep/ .el-drawer__header {
  margin-bottom: 0;
}

/deep/ .el-drawer__body {
  height: calc(100% - 45px - 60px);
}

.el-container {
  text-align: left;
  height: 100%;
}
.el-footer {
  line-height: normal;
  padding-left: 155px;
  height: 40px;
}
</style>