<template>
  <el-upload
      class="upload-demo"
      :class="{hide:!uploadVisible}"
      action="https://jsonplaceholder.typicode.com/posts/"
      :on-preview="handlePreview"
      :on-change="GetUploadVisible"
      :on-remove="handleRemove"
      :file-list="fileList"
      list-type="picture-card"
      :auto-upload="false"
      accept = "png"
      :limit="10"
      :multiple="true"
  >
    <el-icon><plus /></el-icon>
  </el-upload>
</template>
<script>
import {Download,Delete,ZoomIn,Plus} from "@element-plus/icons";

export default {
  data() {
    return {
      fileList: [],
      dialogImageUrl: '',
      uploadVisible: true
    }
  },
  methods: {
    handleRemove(file, fileList) {
      this.fileList = fileList.slice(-10)
      this.uploadVisible = this.fileList.length < 10;
    },
    handlePreview(file) {
    },
    GetUploadVisible(file, fileList) {
      this.fileList = fileList.slice(-10)
      this.uploadVisible = this.fileList.length < 10;
    }
  },
  components: {
    Delete,
    Download,
    ZoomIn,
    Plus
  }
}
</script>

<style scoped>
.hide /deep/ .el-upload--picture-card{
  display: none;
}

</style>