<template>
    <div class="main">
        <div class="sizer" :class="{unfolded:unfolded}">
          <div class="title">{{ info.Title }}</div>
          <div class="sizer-main">
            <sizer-title class="sizer-item"
                         :Items="SizerTitleInfo"
                         :StoreName="StoreName"
                         v-if="SizerTitleShow"
            />
            <sizer-item v-for="(item,index) in info.ItemsList"
                        class="sizer-item"
                        :Items="item"
                        :ItemsIndex="index"
                        :StoreName="StoreName"
            />
          </div>
        </div>
        <div class="set-unfolded">
          <el-icon class="unfold" size="3" @click="unfolded=!unfolded">
            <arrow-down-bold v-if="unfolded" />
            <arrow-up-bold v-if="!unfolded" />
          </el-icon>
        </div>
    </div>
</template>

<script>
import { ArrowDownBold,ArrowUpBold } from '@element-plus/icons';
import SizerItem from "@/common/Sizer/SizerItem";
import SizerTitle from "@/common/Sizer/SizerTitle";

export default {
  name: "Sizer",
  data() {
    return {
      unfolded: true
    }
  },
  computed: {
    info(){
      return this.$store.state[this.StoreName].LoadList.Selected
    },
    SizerTitleShow(){
      return this.info.DateSelect.Usable || this.info.Sort.Usable
    },
    SizerTitleInfo(){
      return {DateSelect:this.info.DateSelect,Sort:this.info.Sort}
    }
  },
  props: {
    StoreName: {type: String, required: true}
  },
  components:{
    ArrowDownBold,
    ArrowUpBold,
    SizerItem,
    SizerTitle
  }
}
</script>

<style scoped>
div {
  margin: 0;
  padding: 0;
}
.main {
  width: 100%;
  line-height: normal;
}
.unfolded {
  height: 30px;
}
.set-unfolded {
  position:relative;
  top:-10px;
  margin-bottom: -10px;
}
.el-icon {
  background: white;
  border-radius:50%
}

.sizer {
  text-align: left;
  display: flex;
  width: 100%;
  overflow: hidden;
  box-shadow: 0 1px 5px #888888;
}
.title {
  flex:0 0 auto;
}
.unfold {
  border: #e9eef3 solid 1px;
}
.sizer-item {
  margin-top: 3px;
  width: 100%;
}
.sizer-main {
  flex:1 1 auto;
  padding-right: 20px;
}
</style>