<template>
  <div>
    <div class="handle-button">
      <span>请点击部件设置规则或删除或</span>
      <el-button type="text" @click="open">新增部件</el-button>
    </div>
    <mountings  :mountings="{}"/>
    <el-dialog v-model="Dialog" title="部件原始图上传（PNG格式，每次最多上传10张）" width="830px" center>
      <div class="upload-box">
        <SelectPic/>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="cancelForm">上传</el-button>
          <el-button type="primary" @click="cancelForm">取消</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import {ElMessageBox} from "element-plus";
import SelectPic from "@/views/banxingguanli/banxingliebiao/modules/funmodules/BanxingGuanli/BxguanliIndex/SelectPic";
import Mountings from "@/views/banxingguanli/banxingliebiao/modules/funmodules/BanxingGuanli/BxguanliIndex/Mountings";

export default {
  name: "Uploader",
  data(){
    return {
      Dialog:false
    }
  },
  components:{
    SelectPic,
    Mountings
  },
  methods:{
    handleClose(done){
      if (this.loading) {
        return
      }
      ElMessageBox.confirm('Do you want to submit?')
          .then(() => {
            this.loading = true
            this.timer = setTimeout(() => {
              done()
              // 动画关闭需要一定的时间
              setTimeout(() => {
                this.loading = false
              }, 400)
            }, 2000)
          })
          .catch(() => {
            this.$emit('dialog',false)
          })
    },
    cancelForm(){
      this.close()
    },
    close(){
      this.Dialog = false
    },
    open(){
      this.Dialog = true
    }
  }
}
</script>

<style scoped>
.upload-box {
  height: 330px;
  width: 100%;
  overflow: hidden;
}
</style>