<template>
  <el-scrollbar wrap-class="scrollbar-wrapper" @scroll="ToTop">
    <div v-infinite-scroll="load" class="infinite-list">
      <div v-for="item in count" class="infinite-list-item"><liebiaoimg :img="item + n" /></div>
    </div>
    <el-backtop target=".scrollbar-wrapper"/>
  </el-scrollbar>
</template>

<script>
import liebiaoimg from "@/views/banxingguanli/banxingliebiao/modules/funmodules/liebiaoimg";

export default {
  name: "liebiaozhanshi",
  data(){
    return {
      count: 40,
      n:0
    }
  },
  components:{
    liebiaoimg
  },
  methods:{
    ToTop(scrollLeft,){
      console.log(scrollLeft);
      if (scrollLeft.scrollTop === 0){
        if (this.n >= 4) {this.n -= 4}
      }
    },
    load(){
      this.count += 4
    }
  }

}
</script>

<style scoped>
.el-scrollbar{
  height: 100%;
  width: 100%;
  text-align: center;
}
.infinite-list .infinite-list-item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  background: var(--el-color-primary-light-9);
  margin: 10px;
  color: var(--el-color-primary);
}
.infinite-list .infinite-list-item + .list-item {
  margin-top: 10px;
}
</style>