<template>
  <div class="main">
    <div class="item-detail">
<!--   时间筛选   -->
      <div class="date-select" v-if="dateShow">
        <el-date-picker
            v-model="date"
            type="daterange"
            range-separator="到"
            start-placeholder="最早上传时间"
            end-placeholder="最晚上传时间"
            size="mini"
        >
        </el-date-picker>
      </div>
<!--   顺序调整   -->
      <div class="sort-select" v-if="sortShow">
        <el-switch
            v-model="sort"
            active-color="#13ce66"
            inactive-color="#ff4949"
            active-text="正序"
            inactive-text="倒序"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "SizerTitle",
  props: {
    Items: {type: Object, required: true},//筛选对象
    StoreName: {type:String, required: true} //Store名称
  },
  computed: {
    sort: {
      get(){
        return this.$store.state[this.StoreName].LoadList.Selected.Sort.info
      },
      set(newVal){
        this.$store.commit(this.StoreName + '/SetSort', newVal)
      }
    },
    date: {
      get(){
        return this.$store.state[this.StoreName].LoadList.Selected.DateSelect.info
      },
      set(newVal){
        this.$store.commit(this.StoreName + '/SetDateSelect', newVal)
      }
    },
    sortShow(){
      return this.$store.state[this.StoreName].LoadList.Selected.Sort.Usable
    },
    dateShow(){
      return this.$store.state[this.StoreName].LoadList.Selected.DateSelect.Usable
    }
  }
}
</script>

<style scoped>
.main {
  width: 100%;
  margin-left: 8px;
}
.item-detail {
  width: 100%;
}
.date-select {
  position:relative;
  top:-2px;
  float: left;
}
.sort-select {
  float: right;
}
</style>