<template>
  <el-form
      ref="ruleForm"
      :model="Form"
      :rules="rules"
      label-width="120px"
      class="demo-ruleForm"
      size="mini"
  >
    <el-form-item label="服装类型" prop="region">
      <el-select v-model="Form.Leixing" placeholder="请选择服装类型">
        <el-option label="T恤" key="Txue" value="Txue"></el-option>
        <el-option label="夹克" key="Jiake" value="Jiake"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="服装风格" prop="region">
      <el-select v-model="Form.Fengge" placeholder="请选择服装风格">
        <el-option label="T恤" key="Txue" value="Txue"></el-option>
        <el-option label="夹克" key="Jiake" value="Jiake"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="服装面料" prop="region">
      <el-select v-model="Form.Mianliao" placeholder="请选择面料">
        <el-option label="T恤" key="Txue" value="Txue"></el-option>
        <el-option label="夹克" key="Jiake" value="Jiake"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="是否自营" prop="delivery">
      <el-switch v-model="Form.Ziying"></el-switch>
    </el-form-item>
    <el-form-item label="拥有尺寸" prop="type">
      <el-checkbox-group v-model="Form.Size" size="mini">
        <el-checkbox-button key="s" label="s">s</el-checkbox-button>
        <el-checkbox-button key="m" label="m">m</el-checkbox-button>
        <el-checkbox-button key="l" label="l">l</el-checkbox-button>
        <el-checkbox-button key="xl" label="xl">xl</el-checkbox-button>
        <el-checkbox-button key="xxl" label="xxl">xxl</el-checkbox-button>
      </el-checkbox-group>
    </el-form-item>
    <el-form-item label="备注" prop="desc">
      <el-input v-model="Form.desc" type="textarea" resize="none"></el-input>
    </el-form-item>
    <el-form-item label="部件管理" prop="type">
      <uploader />
    </el-form-item>
  </el-form>
</template>

<script>
import rules from "@/views/banxingguanli/banxingliebiao/modules/funmodules/BanxingGuanli/BxguanliIndex/Info/rules";
import Uploader from "@/views/banxingguanli/banxingliebiao/modules/funmodules/BanxingGuanli/BxguanliIndex/Uploader";

export default {
  name: "InfoForm",
  data() {
    return {
      Form: {
        Leixing: '',
        Fengge: 'Jiake',
        Mianliao: '',
        Ziying: '',
        Size: ['s','xl'],
        desc: '阿萨德发散',
      },
      rules
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
  },
  components: {
    Uploader
  }
}
</script>

